import React from 'react';
import { NavLink } from "react-router-dom";



const Navbar = () => {
  return(
  <>
  <div className="container-fluid">
    <div className="row">
        <div className="col-md-10 mx-auto">

  <nav className="navbar navbar-expand-lg navbar-light">
   <div className="container-fluid">
    <NavLink className="navbar-brand fw-bold" to="/">SulphiticCo</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mr-auto mb-2 mb-lg-0">
      <li className="nav-item">
          <NavLink activeClassName="menu-active" exact className="nav-link" aria-current="page" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="menu-active" exact className="nav-link" aria-current="page" to="services">Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="menu-active" exact className="nav-link" to="about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="menu-active" className="nav-link" to="contact">Contact Us</NavLink>
        </li>
      </ul>
    </div>
  </div>
  </nav>
  </div>
    </div>
  </div>
      </> 
  );
}; 

export default Navbar;